html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/*
=========================
Variables
=========================
*/
/*
=========================
General Styles
=========================
*/
body {
  background-color: #222222;
  color: #fff;
  line-height: 1.5;
  font-size: 16px; }

a {
  text-decoration: none; }

ul {
  list-style: none; }

.site {
  margin: 20px; }

/*
=========================
Quote Styles
=========================
*/
.site-main h2.entry-title a {
  color: #fff; }

.site-main h2.entry-title:before {
  content: "— "; }

.site-main .entry-content a {
  color: #00cc00; }

/*
=========================
Another Quote Button Styles
=========================
*/
#another-quote {
  padding: 0.75rem 2.25rem;
  background-color: transparent;
  border: 2px solid #00cc00;
  border-radius: 3px;
  color: #00cc00;
  font-weight: bold;
  text-transform: capitalize; }

.site-header .site-title a {
  color: #fff;
  text-transform: lowercase; }

.site-header .site-title em {
  color: #00cc00;
  font-weight: lighter; }

.site-info a {
  color: #00cc00; }

.main-navigation ul {
  display: flex;
  flex-flow: row nowrap;
  padding: 0; }
  .main-navigation ul li:not(:last-child):after {
    content: " | ";
    margin: 0 10px; }

.main-navigation a {
  color: #00cc00; }

/*
=========================
Main Archive Page
=========================
*/
.page section ul {
  display: flex;
  flex-flow: row wrap;
  padding: 0; }
  .page section ul li {
    padding-right: 20px; }
    .page section ul li a {
      color: #00cc00; }

/*
=========================
Categories Page
=========================
*/
.category article, .tag article {
  padding: 20px 0;
  border-bottom: 1px solid #fff; }

.page-template-page-submit form div {
  display: flex;
  flex-flow: column nowrap;
  padding: 0.5rem 0; }
  .page-template-page-submit form div input {
    width: 60%;
    min-width: 250px;
    margin: 0.5rem 0;
    padding: 0.375rem 0.75rem;
    background-color: transparent;
    border: 2px solid #00cc00;
    border-radius: 3px; }
  .page-template-page-submit form div textarea {
    box-sizing: border-box;
    resize: vertical;
    max-height: 50vh;
    height: 10vh;
    width: 100%;
    margin: 0.5rem 0;
    padding: 0.375rem 0.75rem;
    background-color: transparent;
    border: 2px solid #00cc00;
    border-radius: 3px; }

.page-template-page-submit #submit-quote {
  margin: 0.75rem 0;
  padding: 0.375rem 1.5rem;
  background-color: transparent;
  border: 2px solid #00cc00;
  border-radius: 3px;
  color: #00cc00; }
